.tang1beta {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  position: relative; /* Để các marker định vị theo phần tử cha */
}
/* Bọc ảnh bản đồ */
.map-container {
  position: relative; /* Cho phép icon WiFi định vị theo bản đồ */
  height: auto;
}

/* Định dạng ảnh bản đồ */
.map-image {
  max-width: 96%;
  max-height: 90vh;
  width: auto;
  height: auto;
  object-fit: contain;
}

/* Marker của WiFi */
.wifi-marker {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  transform: translate(-50%, -100%); /* Đưa icon WiFi lên trên tên */
  white-space: nowrap; /* Tránh tên WiFi bị xuống dòng */
}

/* Icon WiFi */
.wifi-icon {
  margin-bottom: 5px;
  color: green;
  font-size: 18px;
  
}

/* Tên WiFi */
.wifi-name {
  background-color: white;
  color: blue;
  padding: 1px 5px;
  border-radius: 8px;
  font-size: 11px;
  font-weight: bold;
  text-align: center;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  transition: transform 0.2s ease-in-out; /*Hiệu ứng chuyển động mượt */
}
/* Khi hover vào tên Wifi phóng to lên 1.5 lần */
.wifi-name:hover {
  transform: scale(1.5);
}
/* Đảm bảo layout ổn định trên màn hình nhỏ */
@media screen and (max-width: 768px) {
  .map-image {
      max-width: 100%;
      max-height: 80vh;
  }

  .wifi-icon {
    font-size: 10px !important; /* Giảm kích thước icon */
    width: 16px;
    height: 16px;
  }

  .wifi-name {
    font-size: 3px !important; /* Giảm kích thước chữ */
    padding: 1px 1px; /* Giảm padding để chữ không bị tràn */
  }

  /* Khi hover vào, chỉ phóng to nhẹ */
  .wifi-name:hover {
    transform: scale(1.1);
  }
}
